* {
    box-sizing: border-box;
}

body {
    padding:10px;
    background-color: black;
    color:white;
    text-align: center;
    font-family: sans-serif;
    

}
.main--img {
    border-radius: 10px 10px 0px 0px;
    width: 250px;
    height: 200px;
    
}

h1 {
    margin: 5px;
    font-size: 25px;
    font-weight: 600;
}
h5 {
    margin:0;
    padding-bottom: 20px;
    font-size: 10px;
    font-weight: 400;
    line-height: 0%;
    
}
h6 {
    margin:0px;
    font-weight: 300;
}
.btn_email{
    background-color: white;
    color:#0e76a8;
    padding:8px 40px;
    border-radius: 5px;
    font-family: sans-serif;
    font-weight: 600;
    cursor: pointer;
    margin: 10px 2px 5px 5px;
    
    
}

.btn_linkedin {
    background-color:#0e76a8;
    padding:8px 30px;
    border-radius: 5px;
    font-family: sans-serif;
    font-weight: 600;
    color:white;
    cursor: pointer;
    margin:0px 10px;



}


.btn_linkedin:hover {
        background-color: white;
        color:#0e76a8 ;
}

.btn_email:hover{
    background-color:#0e76a8;
    color:white;   
}
.btn_linkedin{
    text-decoration: none;
    color:white;
}

.main--section{

    text-align: justify;
    padding: 10px;
    margin: auto;
    width: 250px
    


}

h3 {
    padding-top: 10px;
    font-size: small;
    margin: 0px;

}

p {
    font-size:x-small;
    margin-top: 2px;
    
}

.social {
    padding-top:50px;
    margin:0;
    background-color: black;
}


.social > a {
      padding:10px;
      color: rgb(107, 112, 116);
      font-size:larger;
      
}
.social > a:hover {
    color: #0e76a8;
}








